
#wpwrap,
#wpcontent{
  background: var( --mywp-admin--color--white );
}

.wpwrap--has-mywp-admin #wpfooter {

  a {
    color: var(--mywp-admin--color--secondary);
    &:hover,&:focus,&:active{
      color: var(--mywp-admin--color--secondary--hover);
    }
  }

}

#wpbody{

  padding-top: 20px;
  padding-right: 20px;

  @media (max-width: 782px) {
    padding-top: 10px;
    padding-right: 10px;
  }

}

#wpbody-content{
  float: none;
  margin-left: auto;
  margin-right: auto;
  max-width: var( --mywp-admin--page--mw );
}


.mywp-admin{

  font-family: var( --mywp-admin--font--family ) ;

  hr{ margin-top: 40px; }

  hr,
  .mywp-admin-section{

    + .mywp-admin-section{
      margin-top: 40px;
    }
  }

  .mywp-admin-section{

    &.with-background-color{
      position: relative;
      z-index: 1;

      padding-top: var( --mywp-admin--spacing--m );
      padding-bottom: var( --mywp-admin--spacing--m );

      &:before{
        content: '';

        position: absolute;
        z-index: -1;
        left: -20px;
        right: -20px;
        top: 0;
        bottom: 0;
        margin: auto;

        border-radius: 3px;

        background-color: var( --mywp-admin--color--gray--0 );
      }
    }
  }

  .mywp-admin-line{
    display: block;

    margin-top: 10px;
    margin-bottom: 10px;

  }

  p.introduction{
    font-size: 1.3em;
    font-weight: 600;
  }

}
