
.mywp-admin-spacer{

  display: block;
  width: 100%;

  height: var( --mywp-admin--spacing--m );

  &.with-space-xs{
	height: var( --mywp-admin--spacing--xs );
  }

  &.with-space-s{
	height: var( --mywp-admin--spacing--s );
  }

  &.with-space-l{
	height: var( --mywp-admin--spacing--l );
  }

  &.with-space-xl{
	height: var( --mywp-admin--spacing--xl );
  }
}