.mywp-admin-header{

    font-family: var( --mywp-admin--font--family ) ;

    margin-left: -20px;
    padding: 20px;

    color: #fff;
    background-color: #3c2b83;

    @media( max-width:600px ){
        position: relative;
        top: 46px;
        margin-bottom: 46px;
    }

    &--center .mywp-admin-header__inner-container{
        max-width: var( --mywp-admin--page--mw );
        margin-left: auto;
        margin-right: auto;
    }


    &__inner-container{

        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 30px;

        // max-width: var( --mywp-admin--page--mw ) ;
        // margin-left: auto;
        // margin-right: auto;

        @media( max-width:600px ){
            flex-wrap: wrap;
        }
    }

    a{
        &:link,&:hover,&:focus,&:active,&:visited{ color: #fff; text-decoration: underline; }
        &:hover,&:focus,&:active{ text-decoration: none; }
    }


    &__brand{
        display: flex;
        align-items: center;
        gap: 1em;

        /*
        @media (max-width: 600px){
            .mywp-admin-header__logo{ display: none }
        }
        */
    }

    &__title{

        font-size : 30px ;
        line-height: 1em;
        font-weight: 900;

        color: #fff;
        margin-top: 0;
        margin-bottom: 0;

        small{
            display: block;
            font-size: 0.666em;
            font-style: italic;
        }

        span{
            text-transform: uppercase;
        }
    }

    &__logo{}

    &__logo-image{
        display: block;
        max-height: 44px;
    }

    &__help{
        display: flex;
        gap: 15px;
        font-size: 16px;
        justify-content: end;


        @media ( max-width: 600px ) { width: 100%; }

        span{
            display: block;
            text-align: right;
        }
    }

    &__help-icon{
        max-width: 33px;
    }

  }