
label, .label-mimic { font-weight: 600; margin-top: 0; }

input[type="color"],
input[type="date"],
input[type="datetime-local"],
input[type="datetime"],
input[type="email"],
input[type="month"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="time"],
input[type="url"],
input[type="week"],
select,
textarea{
  border: 1px solid var( --mywp-admin--color--gray--60 );

  &:focus{
	border: 1px solid var( --mywp-admin--color--secondary );
	box-shadow: 0 0 0 1px var( --mywp-admin--color--secondary );
  }
}



input[type="checkbox"]:focus,
input[type="color"]:focus,
input[type="date"]:focus,
input[type="datetime-local"]:focus,
input[type="datetime"]:focus,
input[type="email"]:focus,
input[type="month"]:focus,
input[type="number"]:focus,
input[type="password"]:focus,
input[type="radio"]:focus,
input[type="search"]:focus,
input[type="tel"]:focus,
input[type="text"]:focus,
input[type="time"]:focus,
input[type="url"]:focus,
input[type="week"]:focus,
select:focus,textarea:focus{
  border-color: var( --mywp-admin--color--secondary );
  box-shadow: 0 0 0 1px var( --mywp-admin--color--secondary );
  outline: 2px solid transparent;
}


select:focus,
select:hover {
  color: var( --mywp-admin--color--gray--60 );
}

