
:root{

  // basics

  --mywp-admin--color--white      : #fff ;

  --mywp-admin--color--black      : #222 ;
  --mywp-admin--color--dark-black : #000 ;

  // who

  --mywp-admin--color--primary          : #3c2b83 ;
  --mywp-admin--color--primary--hover   : #2d1f60 ;

  --mywp-admin--color--secondary        : #ff3475 ;
  --mywp-admin--color--secondary--hover : #b22351 ;


  // color scale (like wp)
  // https://make.wordpress.org/core/2021/02/23/standardization-of-wp-admin-colors-in-wordpress-5-7/

  --mywp-admin--color--gray--0    : #f6f7f7 ;
  --mywp-admin--color--gray--2    : #f0f0f1 ;
  --mywp-admin--color--gray--5    : #dcdcde ;

  --mywp-admin--color--gray--10   : #c3c4c7 ;
  --mywp-admin--color--gray--20   : #a7aaad ;
  --mywp-admin--color--gray--30   : #8c8f94 ;
  --mywp-admin--color--gray--40   : #787c82 ;
  --mywp-admin--color--gray--50   : #646970 ;
  --mywp-admin--color--gray--60   : #50575e ;
  --mywp-admin--color--gray--70   : #3c434a ;
  --mywp-admin--color--gray--80   : #2c3338 ;
  --mywp-admin--color--gray--90   : #1d2327 ;
  --mywp-admin--color--gray--100  : #101517 ;

}