

.is-mywp-center{
  margin-left: auto;
  margin-right: auto;
}

.is-mywp-page-mw{
  max-width: var( --mywp-admin--page--mw );
}

.is-mywp-overflow-hidden{
  overflow: hidden;
}

.is-mywp-flex{

	display: flex;
  
	flex-wrap: wrap;
  	align-items: center;

  &.with-align-item-start{
	align-items: flex-start;
  }

  &.with-flex-as-grid{

	> div{
	  flex-grow: 1;
	  flex-basis: calc( 33% - ( 2 * var(--mywp-admin--grid--gap) ) );
	  min-width: calc( 33% - ( 2 * var(--mywp-admin--grid--gap) ) );

	}


	.is-mywp-flex__line{

	  flex-basis: 100%;

	  &--w33{ flex-basis: calc( 66% - ( 2 * var(--mywp-admin--grid--gap) ) ); }
	  &--w50{ flex-basis: calc( 50% - ( 2 * var(--mywp-admin--grid--gap) ) ); }
	  &--w66{ flex-basis: calc( 66% - ( 2 * var(--mywp-admin--grid--gap) ) ); }

	  @media(max-width: 600px){ flex-basis: 100%; }
	}

	// &.with-flex-x3{}

  }

  @media ( max-width: 600px ) {
	> div { flex-basis: 100% !important }
  }

}

.is-mywp-grid{

  display: grid;
  grid-template-columns: repeat( auto-fill, minmax( 300px, 1fr) );

  &.with-grid-x2{
	grid-template-columns: 1fr 1fr ;
  }


  &.with-grid-x4{
	grid-template-columns: repeat( auto-fill, minmax( 220px, 1fr) );
  }

  .is-grid-span-2{ grid-column-start: span 2; }

  @media (max-width: 600px) { grid-template-columns: 1fr !important ; }


  &.with-grid-stacked{
	grid-template-columns: 1fr !important ;
  }

}

.is-mywp-flex,
.is-mywp-grid{

  --mywp-admin--grid--gap : var( --mywp-admin--spacing--m ) ;

  gap: var( --mywp-admin--grid--gap );

  &.with-gap-0  { --mywp-admin--grid--gap : var( --mywp-admin--spacing--0 );  }
  &.with-gap-s  { --mywp-admin--grid--gap : var( --mywp-admin--spacing--s );  }
  &.with-gap-m  { --mywp-admin--grid--gap : var( --mywp-admin--spacing--m );  }
  &.with-gap-l  { --mywp-admin--grid--gap : var( --mywp-admin--spacing--l );  }
  &.with-gap-xl { --mywp-admin--grid--gap : var( --mywp-admin--spacing--xl ); }

  &.with-padding-m{
	> div { padding: var( --mywp-admin--spacing--m ) }
  }

  &.with-background-color-alternation{
	> div:nth-child(odd) { background-color: var( --mywp-admin--color--gray--2 ); }
  }
}