
.button,
.button-secondary{

  color: var( --mywp-admin--color--primary );
  border-color: var( --mywp-admin--color--primary );

  &.focus,
  &.hover,
  &:focus,
  &:hover{
	color   : var( --mywp-admin--color--primary--hover );
	border-color : var( --mywp-admin--color--primary--hover );

	// box-shadow: 0 0 0 1px var( --mywp-admin--color--primary--hover );
  }
}


.button-primary{

  color: var( --mywp-admin--color--white );

  background   : var( --mywp-admin--color--primary );
  border-color : var( --mywp-admin--color--primary );

  &.focus,
  &.hover,
  &:focus,
  &:hover{
	color: var( --mywp-admin--color--white );

	background   : var( --mywp-admin--color--primary--hover );
	border-color : var( --mywp-admin--color--primary--hover );

	// box-shadow: 0 0 0 1px var( --mywp-admin--color--primary--hover );
  }

}




.button-alternative{

  color: var( --mywp-admin--color--white );
  background   : var( --mywp-admin--color--secondary );
  border-color : var( --mywp-admin--color--secondary );

  &.focus,
  &.hover,
  &:focus,
  &:hover{
	color: var( --mywp-admin--color--white );
	background   : var( --mywp-admin--color--secondary--hover );
	border-color : var( --mywp-admin--color--secondary--hover );

	// box-shadow: 0 0 0 1px var( --mywp-admin--color--secondary--hover );
  }
}




.button-secondary-alternative{

  color: var( --mywp-admin--color--secondary );
  border-color: var( --mywp-admin--color--secondary );

  &.focus,
  &.hover,
  &:focus,
  &:hover{
	color   : var( --mywp-admin--color--secondary--hover );
	border-color : var( --mywp-admin--color--secondary--hover );
  }
}



.button{

  .dashicons{
	display: flex;
  }
}