h2{
  font-size: 2em;
  font-weight: 800;
  color: var( --mywp-admin--color--primary );
  margin-bottom: 1.55em;

}

h3{
  font-size: 1.45em;
}

h2, h3{

  &.is-mywp-title-underlined{

    padding-bottom: 1em;
    border-bottom: 1px solid var( --mywp-admin--color--gray--2 );

    &.with-underline-color-white{
      border-color: var( --mywp-admin--color--white );
    }

  }
}
